  import WHeader from '../wheader/wheader.vue'
    export default {
        name: 'selectOne',
        data() {
            return {
                provList: provs_data,
              headerTitle: ""
            }
        },
      components:{
        WHeader
      },
      methods:{
        getCity(id,name){
          //
          var cope = {
            provinceName: "",
            provinceId: "",
            cityName:"",
            cityId: "",
            countyName:"",
            countyId: ""
          };
          cope.provinceName = name;
          cope.provinceId = id;
          this.$store.state.threeArea = cope;
//          console.log(cope);
          //
          if(citys_data[id]){
            this.$router.push("/city");
          }else{
            //
            this.$router.push("/");
          }

        }
      }
    }



