import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
// import Toast from '../modules/w-toast/w-toast'
import WHeader from '../modules/wheader/wheader.vue'
import agreement from '../agreement/agreement.vue'

export default {
  name: 'aboutUs',
  data() {
    return {
      payType: "1",
      nodeText: '',
      second: 30,
      isDisabled: false,nodeText: '',
      second: 30,
      isDisabled: false,
      headerTitle: "",
      showAgreement: false,
      deal: '',
      message: {
        id: '',
        name: '',
        ppa: '1',
        phone: '',
        type: '2',
        checkCode: ''
      }
    }
  },
  created() {
    var userInfo = this.getLocation('userInfo');
    this.deal = userInfo.deal;
    //id
    this.message.id = userInfo.id;
  },
  components: {
    Checker,
    CheckerItem,
    // Toast,
    WHeader,
    agreement
  },
  methods: {
    test() {
      alert('111');
    },
    //
    getCode() {
      if (!this.message.phone) {
        this.showToast("", 1, 1500);
        return;
      }
      var API = this.$store.state.api;
      if (this.isDisabled === false) {
        this.$http.post(API.sendMsg, {
          phone: this.message.phone,
          TemplateCode: 'SMS_91130050'
        }, {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
            emulateJSON: true
          }).then((res) => {
            var result = res.body;
            if (result.state == 1) {
              this.showToast("", 1, 1500);
            } else {
              this.showToast(result.message, 2, 1500);
            }
          });
        this.startTime();
      }
    },
    //
    startTime() {
      var self = this;
      //
      if (this.isDisabled === true) return;
      this.isDisabled = true;
      var s = self.second;
      self.nodeText = s + "";
      var timer = setInterval(() => {
        s--;
        //
        if (s > 0) {
          self.nodeText = s + "";
        } else {//
          self.isDisabled = false;
          // self.second = 60;
          self.nodeText = "";
          clearInterval(timer);
        }
      }, 1000);
    },
    //
    submit() {
      if (!this.message.name) {
        this.showToast("", 1, 1500);
        return;
      } else if (!this.message.phone) {
        this.showToast("", 1, 1500);
        return;
      } else if (!this.message.checkCode) {
        this.showToast("", 1, 1500);
        return;
      }
      var API = this.$store.state.api;
      var self = this;
      this.$http.post(API.userUpdate, this.message, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
        emulateJSON: true
      }).then((res) => {
        var result = res.body;
        if (result.state == 1) {
          this.showToast("", 1, 1500);
          //
          var timer = setTimeout(function() {
            self.$router.push('/');
            clearTimeout(timer);
          }, 1500);
        } else {
          this.showToast(result.message, 2, 1500);
        }
      });

    },
    //
    lookAgreement() {
      this.$store.state.showAgreement = true;
    },
    //
    hideAgreement() {
      this.$store.state.showAgreement = false;
    }
  }
}



