import WHeader from '../modules/wheader/wheader.vue'

export default {
  name: 'orderList',
  data() {
    return {
      defaultImg: 'this.src="' + require('../../../static/img/pic.png') + '"',
      headerTitle: "",
      menuList: [{ name: '', value: '3' }, { name: '', value: '1' }, { name: '', value: '2' }],
      ListIndex: '0',
      showSuccessToast: false,
      pageList: [],
      imagesList: [],
      message: {
        page: -1,
        pageSize: 5,
        type: "1",
        userid: ""
      },
      mescroll: null
    }
  },
  components: {
    WHeader
  },
  mounted() {
    //
    var userInfo = this.getLocation("userInfo");
    this.message.userid = userInfo.id;
    var roomsIndex = this.getLocation('myRoomsTypeIndex');
    this.ListIndex = roomsIndex?roomsIndex:'0';
    this.message.type = this.menuList[this.ListIndex].value;

    //MeScroll
    this.mescroll = new MeScroll("mescroll", {
      down: {
        auto: false, //callback; true
        callback: this.downCallback //
      },
      up: {
        auto: true, //; false
        callback: this.upCallback //,;  callback: function (page) { upCallback(page); }
      }
    });
  },
  methods: {
    downCallback() {
      this.message.page = 0;
      this.getMyRooms('pull');//
    },
    upCallback() {

      var API = this.$store.state.api;
      this.message.page++;

      this.$http.post(API.getMyRooms, this.message, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
        emulateJSON: true
      }).then((res) => {
        var result = res.body;
        if (result.state == 1) {
          var data = result.data;
          this.pageList = this.pageList.concat(data);
          this.mescroll.endSuccess(data.length);
        } else {
          this.showToast(result.message, 2, 1500);
          //,
          this.mescroll.endErr();
        }
      });

    },
    toOrderDetail() {
      this.$router.push('orderDetail');
    },
    myRoomDetail(val) {
      this.$router.push("/dayRentDetail");
      this.setLocation("houseId", val.house.id); //id
    }, //
    getOBType(index) {
      this.ListIndex = index;
      this.setLocation('myRoomsTypeIndex',index);
      this.message.type = this.menuList[index].value;
      this.message.page = 0;
      this.getMyRooms();
    }, //
    getMyRooms(type) {
      var API = this.$store.state.api;
      var isPull = type ==='pull';

      //
      !isPull&&this.showLoading("...");
      this.$http.post(API.getMyRooms, this.message, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
        emulateJSON: true
      }).then((res) => {
        // var result = JSON.parse(res.bodyText);
        var result = res.body;
        !isPull&&this.hideLoading();
        if (result.state == 1) {
          var data = result.data;
          this.pageList = data; //
          //,
          this.mescroll.endSuccess(data.length);
        } else {
          this.pageList = [];
          this.showToast(result.message, 2, 1500);
          //,
          this.mescroll.endErr();
        }
      });
    },//
    editRoom(val) {
      this.$router.push("/releaseRoom");
      this.setLocation("houseId", val.house.id);//id
    },//
    deleteRoom(val,index) {
      var that = this;
      var API = this.$store.state.api;
      var ID = val.house.id;

      if (ID) {
        this.showPop('', '', '', function() {
          //
          that.closePop();
          
          that.showLoading("...");
          that.$http.post(API.deleteHouse, { ids: ID }, {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
            emulateJSON: true
          }).then((res) => {
            var result = res.body;
            if (result.state == 1) {
              that.showToast("", 1, 1500);
              that.pageList.splice(index,1);
              that.hideLoading();
              // this.getMyRooms(); //
            } else {
              that.showToast(res.message, 2, 1500);
            }
          });
        });
      }

    },//
  }
}



