import Vue from 'vue'
import Vuex from 'vuex'
import vuexI18n from 'vuex-i18n'
import config from '../config'

Vue.use(config);
Vue.use(Vuex);

//vux 
let store = new Vuex.Store({
  modules: {
    i18n: vuexI18n.store
  }
});

Vue.use(vuexI18n.plugin, store);

export default new Vuex.Store({
  state: {
    tradingType: "1", //
    userType: '1', //
    userInfo:{}, //
    token: '',//
    openid: '',//
    toastConfig: {
      title:"",
      type: 2
    },//
    footerSelect: 1,
    threeArea:{
      provinceName: "",
      provinceId: "",
      cityName:"",
      cityId: "",
      countyName:"",
      countyId: ""
    },
    showPop: false,
    popConfig: {
      title:"",
      content: '',
      buttonName: ''
    },
    imgIndex: '', //
    loadConfig:{
      loadShow: false,
      loadText: ""
    },
    roomDetailInfo:{},//
    api: config.API,
    facilityList: [], //
    showSuccessToast: false,
    showAgreement: false

  },
  mutations: {


  }
})



